body {
  background-color: rgba(246, 246, 246, 0.6);
  font-size: 14px;
  overflow-x: hidden;
  font-family: Raleway;
  color: #333333; }

ul {
  padding-left: 0;
  list-style-type: none;
  margin-bottom: 0; }

* a {
  color: #ab8ce4;
  text-decoration: none; }

a:not([href]):not([tabindex]) {
  color: inherit; }

*.btn:focus {
  box-shadow: none !important; }

p {
  font-size: 13px;
  line-height: 1.7;
  letter-spacing: 0.7px; }

code {
  color: #ab8ce4 !important;
  background-color: #eeeeee;
  padding: 3px;
  margin: 0 3px;
  border-radius: 2px; }

blockquote {
  border-left: 4px solid #cccccc;
  padding: 15px; }

figure.text-center blockquote {
  border: none;
  padding: 15px; }

figure.text-end blockquote {
  border-left: none;
  border-right: 4px solid #cccccc;
  padding: 15px; }

.media {
  display: flex; }
  .media .media-body {
    flex: 1; }

:focus {
  outline-color: #ab8ce4; }

.disabled {
  cursor: not-allowed;
  opacity: 0.5; }

.row > div {
  position: relative; }

.ace-editor {
  height: 400px;
  width: 100%; }

.editor-toolbar {
  width: 100% !important; }
  .editor-toolbar.fullscreen {
    z-index: 100; }

.CodeMirror {
  top: 0 !important; }
  .CodeMirror.CodeMirror-fullscreen {
    z-index: 100;
    top: 50px !important; }

.typography h1,
.typography .h1,
.typography h2,
.typography .h2,
.typography h3,
.typography .h3,
.typography h4,
.typography .h4,
.typography h5,
.typography .h5,
.typography h6,
.typography .h6 {
  margin: 20px 0;
  margin-top: 0; }
