.typography small {
  padding-left: 10px;
  color: #2a3142; }

pre {
  background-color: #f9f7ff;
  padding: 20px 0; }

a:hover {
  text-decoration: none; }

.owl-theme .owl-dots .owl-dot span {
  background: #e7dff7; }

.owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span {
  background: #ab8ce4; }

.owl-theme .owl-nav.disabled + .owl-dots {
