  .switch input {
    display: none; }
    .switch input:checked + .switch-state {
      background-color: #ab8ce4; }
      .switch input:checked + .switch-state:before {
        transform: translateX(26px); }
    .switch input:focus + .switch-state {
      box-shadow: 0 0 1px #ab8ce4; }

.switch-state {
  border-radius: 20px;
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #eeeeee;
  transition: .4s; }
  .switch-state:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: #fff;
    transition: .4s;
    border-radius: 20px; }

.icon-state .switch-state:after {
  content: "\efb8";
  font-family: IcoFont;
  left: 10px;
  position: absolute;
  top: 11px;
  color: #2a3142;
  transition: .4s;
  opacity: 0.3; }

.icon-state input:checked + .switch-state:after {
  content: "\efad";
  left: 35px;
  opacity: 1;
  transition: .4s; }

.switch-lg .switch {
  width: 75px;
