%chart-1 {
  width: 70px;
  height: 70px;
  margin-left: 15px;
  margin-top: 15px;
  line-height: 70px;
}
%chart-2 {
  width: 35px;
  height: 35px;
  margin-left: 7.5px;
  margin-top: 7.5px;
  line-height: 35px;
}
%chart-3 {
  width: 20px;
  height: 20px;
  margin-left: 5.5px;
  margin-top: 4.5px;
  line-height: 21px;
}


.chart-block {
  canvas {
    width: 100% !important;
  }
  .chart-overflow {
    overflow: hidden;
    text-align: center;
  }
}
.candidcahrt {
  width: 100%;
  height: 470px;
}
.amcharts-export-canvas {
  position: absolute;
  display: none;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: $white;
  .canvas-container {
    transition: opacity .5s ease-out;
  }
  &.active {
    display: block;
  }
  &.dropbox .canvas-container {
    opacity: .5;
  }
}
.flot-chart-container {
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  width: 100%;
  height: 400px;
  #toggling-series-flot {
    display: inline-block;
    width: 79% !important;
  }
}
.flot-chart-placeholder {
  width: 100%;
  height: 100%;
  font-size: 14px;
  line-height: 1.2em;
  text-align: center;
  .legend table {
    border-spacing: 5px;
  }
}
.morris-hover {
  position: absolute;
  z-index: 8;
  &.morris-default-style {
    border-radius: 10px;
    padding: 6px;
    color: $dark-gray;
    background: $white;
    border: solid 1px $light-gray;
    font-family: $font-serif;
    font-size: 12px;
    text-align: center;
    .morris-hover-row-label {
      font-weight: bold;
      margin: 0.25em 0;
    }
    .morris-hover-point {
      white-space: nowrap;
      margin: 0.1em 0;
    }
  }
}
.chart-sparkline {
  position: absolute;
  left: 0;
  top: 0;
  visibility: hidden;
  background: lighten($black , 60%) transparent;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000);
  color: $white;
  font: 10px $font_arial, $font-serif;
  text-align: left;
  white-space: nowrap;
  padding: 5px;
  border: 1px solid $white;
  box-sizing: content-box;
  z-index: 10000;
  .jqsfield {
    color: $white;
    font: 10px $font_arial, $font-serif;
    text-align: left;
  }
}
.radial-bar {
  position: relative;
  display: inline-block;
  border-radius: 50%;
  background-color: transparent;
  box-sizing: content-box;
  width: 80px;
  height: 80px;
  font-size: 18px;
  font-family: $font-open-sans, $font-serif;
  background-clip: content-box;
  &:after {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    text-align: center;
    font-weight: 500;
    color: $color-fiord;
    content: attr(data-label);
    background-color: $white;
    z-index: 1;
    width: 56px;
    height: 56px;
    margin-left: 12px;
    margin-top: 12px;
    line-height: 56px;
  }
  > img {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    text-align: center;
    font-weight: 500;
    color: $color-fiord;
    z-index: 3;
    width: 56px;
    height: 56px;
    margin-left: 12px;
    margin-top: 12px;
    line-height: 56px;
  }
  &.radial-bar-35 {
    background-image: linear-gradient(90deg, $light-semi-gray 50%, transparent 50%, transparent), linear-gradient(216deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }
  &.radial-bar-50 {
    background-image: linear-gradient(270deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }
  &.radial-bar-60 {
    background-image: linear-gradient(306deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }
  &.radial-bar-65 {
    background-image: linear-gradient(324deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }
  &.radial-bar-70 {
    background-image: linear-gradient(342deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }
  &.radial-bar-75 {
    background-image: linear-gradient(360deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }
  &.radial-bar-80 {
    background-image: linear-gradient(378deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }
  &.radial-bar-85 {
    background-image: linear-gradient(396deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }
}
.radial-bar-primary {
  &.radial-bar-35 {
    background-image: linear-gradient(90deg, $light-semi-gray 50%, transparent 50%, transparent), linear-gradient(216deg, $primary-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }
  &.radial-bar-50 {
    background-image: linear-gradient(270deg, $primary-color 50%, transparent 50%, transparent), linear-gradient(270deg, $primary-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }
}
.radial-bar-secondary {
  &.radial-bar-60 {
    background-image: linear-gradient(306deg, $secondary-color 50%, transparent 50%, transparent), linear-gradient(270deg, $secondary-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }
}
.radial-bar-success {
  &.radial-bar-65 {
    background-image: linear-gradient(324deg, $success-color 50%, transparent 50%, transparent), linear-gradient(270deg, $success-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }
}
.radial-bar-info {
  &.radial-bar-75 {
    background-image: linear-gradient(360deg, $info-color 50%, transparent 50%, transparent), linear-gradient(270deg, $info-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }
  &.radial-bar-85 {
    background-image: linear-gradient(396deg, $info-color 50%, transparent 50%, transparent), linear-gradient(270deg, $info-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }
}
.radial-bar-danger {
  &.radial-bar-50 {
    background-image: linear-gradient(270deg, $danger-color 50%, transparent 50%, transparent), linear-gradient(270deg, $danger-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }
  &.radial-bar-80 {
    background-image: linear-gradient(378deg, $danger-color 50%, transparent 50%, transparent), linear-gradient(270deg, $danger-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }
}
.pieLabel{
  div {
    color: $white !important;
    input[type=checkbox] {
      margin: 7px;
    }
  }
}
// knob chart css
.knob-chart{
  min-height: 255px;
  .chart-clock-main{
    position:relative;
    width:200px;
    margin:45px auto;
    .clock-large{
      position:absolute;
      left:-48px;
      top:-45px;
    }
    .clock-medium{
      position:absolute;
      left:2px;
      top:5px;
    }
    .clock-small{
      position:absolute;
      left:14px;
      top:55px;
    }
  }
}
.knob-block {
  position: relative;
  margin-bottom: -10px;
  input {
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    height: 100% !important;
    width: 100% !important;
    left: 0 !important;
    top: 0 !important;
    margin-left: 0 !important;
    margin-top: -3px !important;
  }
}
.knob-disable {
  input {
    display: none !important;
  }
}
